import React, { useState, useRef } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import SpeedDial from '@material-ui/lab/SpeedDial'
import SpeedDialAction from '@material-ui/lab/SpeedDialAction'

// import useNewImageMenu from '../PhotoSeries/views/controls/useNewImageMenu'
import AddCameraIcon from '@material-ui/icons/AddAPhoto'
import PhotoLibraryIcon from '@material-ui/icons/PhotoLibrary'
import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles(theme => ({
  root: {
    position: 'absolute',
    bottom: theme.mixins.toolbar / 2,
    left: '50%',
    transformOrigin: '50% 50%',
    transform: 'translateY(-50%) translateX(-50%)'
  },
  speedDialFab: {
    background: theme.palette.secondary.main,
    color: theme.palette.secondary.contrastText
  },
  directionUp: {
  }
}))

const AddFab = ({ className, children, onCameraClick, onLibraryClick }) => {
  const [open, setOpen] = useState(false)
  const [down, setDown] = useState(false)
  const classes = useStyles()
  // const theme = useTheme()
  const downRef = useRef(down)
  downRef.current = down

  const startTimer = (el) => {
    setDown(true)
    if (!open) {
      setTimeout(() => {
        if (downRef.current) setOpen(true)
      }, 300)
    }
  }

  // const onUp = (e) => {
  //   // console.log('onUp', e.currentTarget)
  //   // setSeries(series)
  //   setDown(false)
  //   setOpen(false)
  //   // onCameraClick()
  // }

  const onDown = (e) => {
    // console.log('onDown', e.currentTarget)
    // setSeries(series)
    startTimer(e.currentTarget)
  }

  const block = (e) => {
    if (e && e.stopPropagation) {
      e.stopPropagation()
      e.preventDefault()
    }
  }

  return (
    <SpeedDial
      ariaLabel='SpeedDial photo adder'
      onBlur={(e) => {
        setTimeout(() => setOpen(false), 250)
      }}
      classes={{
        directionUp: classes.directionUp,
        root: classes.root,
        fab: classNames(classes.speedDialFab, className)
      }}
      icon={children}
      onClick={(e) => {
        block(e)
        setDown(false)
        if (!open) {
          console.log('cameraclick MAIN')
          onCameraClick()
        }
      }}
      onMouseDown={onDown}
      // onMouseUp={onUp}
      onTouchStart={onDown}
      // onTouchEnd={onUp}

      open={open}
      direction='up'
    >
      <SpeedDialAction
        // Bit of a hack here. This function isn't quite ready for primetime
        icon={<AddCameraIcon className={classes.speedDialIcon} />}
        tooltipPlacement='right'
        tooltipTitle='From Camera'
        tooltipOpen
        onClick={(e) => {
          console.log('cameraclick')
          setOpen(false)
          setDown(false)
          onCameraClick()
          block(e)
        }}
      />
      <SpeedDialAction
        // Bit of a hack here. This function isn't quite ready for primetime
        icon={<PhotoLibraryIcon className={classes.speedDialIcon} />}
        tooltipPlacement='right'
        tooltipTitle='From Photo Library'
        tooltipOpen
        onClick={(e) => {
          console.log('libraryClick')
          setOpen(false)
          setDown(false)
          onLibraryClick()
          block(e)
        }}
      />
    </SpeedDial>
  )
}

AddFab.propTypes = {
  className: PropTypes.string,
  children: PropTypes.any,
  onCameraClick: PropTypes.func.isRequired,
  onLibraryClick: PropTypes.func.isRequired
}

export default AddFab
