export default async function doUpload (activeUpload, updateUploadProgress, completeActiveUpload) {
  console.log('UploadConntect: upload start', activeUpload)

  // Initialising the HTML POST form for posting to S3 directly
  let formData = new FormData()
  // Take the auth credentials that we got from the backend and add them to our S3 POST
  Object.keys(activeUpload.preAuth.fields).forEach(fieldkey => {
    console.log('APPEND', fieldkey, activeUpload.preAuth.fields[fieldkey])
    formData.append(fieldkey, activeUpload.preAuth.fields[fieldkey])
  })
  const file = dataURLtoFile(activeUpload.base64, 'a.png')
  formData.append('file', file)

  const config = {
    headers: {
      'Content-Type': 'multipart/form-data'
    },
    onUploadProgress: progressEvent => {
      const { loaded, total } = progressEvent
      // Do something with the progress details
      console.log('PROGRESSEVENT', loaded, total, Math.floor(100 * loaded / total))
      updateUploadProgress({ progress: Math.floor(100 * loaded / total) })
    }
  }

  console.log('FORMDATA', formData)
  // Upload file to S3
  await axios.post(
    activeUpload.preAuth.url,
    formData,
    config
  ).catch(err => {
    console.error(err)
    console.log(err.stack)
  })
  console.log('UploadConntect: upload done')
  completeActiveUpload()
}

function dataURLtoFile (dataurl, filename) {
  var arr = dataurl.split(','); var mime = arr[0].match(/:(.*?);/)[1]

  var bstr = atob(arr[1]); var n = bstr.length; var u8arr = new Uint8Array(n)
  while (n--) {
    u8arr[n] = bstr.charCodeAt(n)
  }
  return new File([u8arr], filename, { type: mime })
}
