import React, { useContext } from 'react'
import PropTypes from 'prop-types'
import { home } from '../config'
import permissions from '../permissions'
import { graphql, compose } from 'react-apollo'
import { connect } from 'react-redux'
import actions from '../redux/actions'
import Loading from '../components/auth/Loading'
import { getWholeSeries, allSeries } from './queries'
import { deletePhoto, deleteSeries, updateSeries, createPhoto, createSeries, updatePhoto, watchSeries, unWatchSeries } from './mutations'
import RepeatablePhotos, { MODES } from '../components/PhotoSeries'
import { localUUID } from '../lib/localdb'
import { AppContext } from '../contexts'

const SeriesConnect = ({
  series, loading, domain, activeSeries, initialPhoto,
  onAfterSeriesDelete, onBack, queueUpload, doneCreatingSeries,
  createSeriesMutation, createPhotoMutation,
  updateSeriesMutation, updatePhotoMutation,
  deleteSeriesMutation, deletePhotoMutation,
  watchSeriesMutation, unWatchSeriesMutation
}) => {
  const { role, currentUser, seriesConfig, user, signedIn } = useContext(AppContext)
  let inputMethod = 'camera'
  // Hack until we can get good loading object sorted
  if (domain === home.AppStateTypes.SERIES) {
    if (!series && !loading) return 'No series...'
    else if (loading) return <Loading />
  }
  else {
    if (domain === home.AppStateTypes.NEW_SERIES_CAMERA) inputMethod = 'camera'
    else if (domain === home.AppStateTypes.NEW_SERIES_LIBRARY) inputMethod = 'library'
  }

  // console.log('SeriesConnect', { series, loading, currentState, activeSeries, role, currentUser })
  let modeName = MODES.SERIES_VIEW
  if (domain !== home.AppStateTypes.SERIES) modeName = MODES.NEWSERIES

  return <RepeatablePhotos
    modeName={modeName}
    inputMethod={inputMethod}

    owned={signedIn && user && series && user.id === series.id}
    watching={signedIn && user && user.watching && user.watching.indexOf(activeSeries) > -1}

    // Everything we need to determine access
    currentUserId={currentUser.attributes.sub}
    currentUserRole={role}
    accessControl={permissions}

    series={series}
    initialPhoto={initialPhoto}
    seriesConfig={seriesConfig}
    // Now some functions to handle actions we might take
    onNewPhoto={({ base64, ...newPhoto }) => {
      console.log('onNewPhoto', { base64, ...newPhoto })
      const ext = base64.split(';')[0].split('/')[1]
      return createPhotoMutation(newPhoto, ext)
        .then(({ data }) => {
          // Don't start an upload until you get back a proper id
          if (data.createPhoto.preAuth) {
            const preAuth = JSON.parse(data.createPhoto.preAuth)
            console.log('PREAUTH', data.createPhoto.preAuth)
            queueUpload({
              id: newPhoto.id,
              seriesId: data.createPhoto.seriesId,
              base64,
              preAuth
            })
          }
          return data
        })
        .catch(err => {
          console.log('onNewPhoto Graphql Error', err.stack)
          console.error(err)
        })
    }}
    onChangePhoto={(photo, photoDelta) => updatePhotoMutation(photo, photoDelta)
      .catch(err => {
        console.log('onChangePhoto Graphql Error', err.stack)
        console.error(err)
      })
    }
    onDeletePhoto={(photo) => deletePhotoMutation(photo.id)
      .then((...data) => {
        console.log('photo deleted', data)
      })
      .catch(err => {
        console.log('onDeletePhoto Graphql Error', err.stack)
        console.error(err)
      })
    }
    onChange={(series, seriesDelta) => updateSeriesMutation(series, seriesDelta)
      .catch(err => {
        console.log('onChange Graphql Error', err.stack)
        console.error(err)
      })
    }
    onNewSeries={(newSeries, photoNoId) => {
      console.log('Series.connect: onNewSeries starting', newSeries)
      return createSeriesMutation(newSeries)
        .then(async ({ data }) => {
          const { base64, ...newPhoto } = photoNoId
          const ext = base64.split(';')[0].split('/')[1]
          console.log('Series.connect: done createSeriesMutation', data)
          const { data: photoData } = await createPhotoMutation({ ...photoNoId, seriesId: data.createSeries.id }, ext)

          const preAuth = JSON.parse(photoData.createPhoto.preAuth)
          console.log('PREAUTH', photoData.createPhoto.preAuth)
          queueUpload({
            id: newPhoto.id,
            seriesId: photoData.createPhoto.seriesId,
            base64,
            preAuth
          })
          doneCreatingSeries(data.createSeries.id)
          return data
        })
        .catch(err => {
          console.log('onNewSeries Graphql Error', err.stack)
          console.error(err)
        })
    }}
    onDelete={() => deleteSeriesMutation()
      .then(onAfterSeriesDelete)
      .catch(err => {
        console.log('onDelete Graphql Error', err.stack)
        console.error(err)
      })
    }
    onClose={onBack}
    onWatch={signedIn ? watchSeriesMutation : null}
    onUnWatch={signedIn ? unWatchSeriesMutation : null}
  />
}
SeriesConnect.propTypes = {
  domain: PropTypes.string,
  activeSeries: PropTypes.string,
  initialPhoto: PropTypes.string,

  // From Redux
  onAfterSeriesDelete: PropTypes.func,
  doneCreatingSeries: PropTypes.func,
  queueUpload: PropTypes.func,
  onBack: PropTypes.func,

  // Data from apollo
  series: PropTypes.object,

  // Functions From Apollo
  loading: PropTypes.bool,
  updateSeriesMutation: PropTypes.func,
  updatePhotoMutation: PropTypes.func,
  deleteSeriesMutation: PropTypes.func,
  deletePhotoMutation: PropTypes.func,
  createSeriesMutation: PropTypes.func,
  createPhotoMutation: PropTypes.func,
  watchSeriesMutation: PropTypes.func,
  unWatchSeriesMutation: PropTypes.func
}

/**
 * Here's the graphql HOC that gets an individual series
 */
const withGetSelectedQuery = graphql(getWholeSeries, {
  props: ({ data }) => {
    if (data.error) console.error(data.error)
    // console.log('HERE', data)
    let series = null
    try {
      if (!data.loading && !data.error) series = data.series
    }
    catch (e) {
      console.error(data)
      console.error(e)
    }
    return {
      data,
      loading: data.loading,
      series
    }
  },
  skip: ({ activeSeries, isNewSeries }) => !activeSeries || isNewSeries,
  options: ({ activeSeries }) => ({
    variables: {
      id: activeSeries
    }
  })
})

const withUpdateSeriesMutation = graphql(updateSeries, {
  props: ({ mutate, ownProps }) => ({
    // We turn this into a thunk so we can handle the business logic here and not in a JSX region
    updateSeriesMutation: (series, seriesDelta) => mutate({
      // input UpdateSeries {
      //   id: ID!
      //   name: String
      //   note: String
      //   latitude: Float
      //   longitude: Float
      //   direction: Int
      // }
      variables: { series: sanitizeUpdateParams(seriesDelta, ['id', 'name', 'access', 'note', 'latitude', 'longitude', 'direction']) },
      optimisticResponse: {
        updateSeries: {
          ...series,
          ...seriesDelta,
          __typename: 'Series'
        }
      },
      update: (store, { data }) => {
        // console.log('withUpdateSeriesMutation start Manip:', JSON.stringify(data, null, 2))
        const qdata = store.readQuery({ query: getWholeSeries, variables: { id: ownProps.activeSeries } })
        qdata.series = { ...qdata.series, ...seriesDelta }
        store.writeQuery({ query: getWholeSeries, data: qdata })
        // console.log('withUpdateSeriesMutation Done Manip:', qdata)

        // Update: allSeries
        // We might not have called this query yet so let it throw if it wants
        try {
          const qdataAll = store.readQuery({ query: allSeries, variables: { limit: 100 } })
          // console.log('withUpdateSeriesMutation qdataAll:', qdataAll)
          const summary = sanitizeUpdateParams(seriesDelta, ['id', 'name', 'access', 'latitude', 'longitude', 'direction'])
          qdataAll.allSeries.seriesSummaries = qdataAll.allSeries.seriesSummaries.map(s => s.id !== summary.id
            ? s
            : { ...s, ...summary }
          )
          store.writeQuery({ query: allSeries, data: qdataAll })
          // console.log('withUpdateSeriesMutation qdataAll WRITE:', qdataAll)
        }
        catch (e) { }
      }
    })
  })
})

// id: ID!
// seriesId: ID!
// takenOn: String
// tags: [TagInput]
// note: String
const withUpdatePhotoMutation = graphql(updatePhoto, {
  props: ({ mutate, ownProps }) => ({
    // We turn this into a thunk so we can handle the business logic here and not in a JSX region
    updatePhotoMutation: (photo, photoDelta) => {
      const sanitizedPhotoDelta = sanitizeUpdateParams(photoDelta, [ 'id', 'seriesId', 'takenOn', 'tags', 'note' ])
      if (sanitizedPhotoDelta.tags && sanitizedPhotoDelta.tags.length > 0) {
        // input TagInput {
        //   label: String
        //   typeId: String,
        //   coords: ImageCoordsInput
        // }
        sanitizedPhotoDelta.tags = sanitizedPhotoDelta.tags.map(t => {
          const newT = sanitizeUpdateParams(t, [ 'label', 'typeId', 'coords' ])
          if (newT.coords) newT.coords = sanitizeUpdateParams(newT.coords, [ 'x', 'y' ])
          return newT
        })
      }
      // console.log('withUpdatePhotoMutation', sanitizedPHoto)
      return mutate({
      // input UpdatePhoto {
      //   id: ID!
      //   seriesId: ID!
      //   takenOn: String
      //   tags: [TagInput]
      //   note: String
      // }
        variables: { photo: sanitizedPhotoDelta },
        optimisticResponse: {
          updatePhoto: {
            ...photo,
            ...sanitizedPhotoDelta,
            __typename: 'Photo'
          }
        },
        update: (store, { data }) => {
          // console.log('updatePhotoMutation start Manip:', JSON.stringify(data, null, 2))
          const qdata = store.readQuery({ query: getWholeSeries, variables: { id: ownProps.activeSeries } })
          // Gotta add Typenames
          const updatedPhoto = {
            ...sanitizedPhotoDelta,
            tags: sanitizedPhotoDelta.tags ? sanitizedPhotoDelta.tags.map(t => {
              const newT = { ...t, __typename: 'Tag' }
              newT.coords = newT.coords ? { ...newT.coords, __typename: 'ImageCoords' } : null
              return newT
            }) : []
          }
          // console.log('updatePhotoMutation', updatedPhoto)
          qdata.series.photos = qdata.series.photos.map(photo => photo.id !== photoDelta.id ? photo : { ...photo, ...updatedPhoto })

          store.writeQuery({ query: getWholeSeries, data: qdata })
          // console.log('updatePhotoMutation Manip:', { qdata })
        }
      })
    }
  })
})

const withDeleteSeriesDeleteMutation = graphql(deleteSeries, {
  props: ({ mutate, ownProps }) => {
    return {
      deleteSeriesMutation: () => mutate({
        variables: { id: ownProps.activeSeries },
        optimisticResponse: {
          deleteSeries: ownProps.activeSeries
        },
        update: (store, { data }) => {
          try {
            const qdataAll = store.readQuery({ query: allSeries, variables: { limit: 100 } })
            qdataAll.allSeries.seriesSummaries = qdataAll.allSeries.seriesSummaries.filter(s => s.id !== data.deleteSeries)
            store.writeQuery({ query: allSeries, data: qdataAll })
          }
          catch (e) { }
        }
      })
    }
  }
})

const withDeletePhotoMutation = graphql(deletePhoto, {
  props: ({ mutate, ownProps }) => ({
    // We turn this into a thunk so we can handle the business logic here and not in a JSX region
    deletePhotoMutation: (photoId) => mutate({
      variables: { seriesId: ownProps.series.id, photoId },
      optimisticResponse: {
        deletePhoto: photoId
      },
      update: (store, { data }) => {
        console.log('deletePhotoMutation start Manip:', JSON.stringify(data, null, 2))
        let finalCount = null

        const qdata = store.readQuery({ query: getWholeSeries, variables: { id: ownProps.activeSeries } })
        qdata.series.photos = qdata.series.photos.filter(photo => photo.id !== data.deletePhoto)

        finalCount = qdata.series.photos.length
        qdata.series.photoCount = finalCount
        store.writeQuery({ query: getWholeSeries, data: qdata })
        console.log('deletePhotoMutation start Manip WRITE:', qdata)

        // Update: allSeries
        // We might not have called this query yet so let it throw if it wants
        try {
          const qdataAll = store.readQuery({ query: allSeries, variables: { limit: 100 } })
          console.log('deletePhotoMutation qdataAll:', qdataAll)
          qdataAll.allSeries.seriesSummaries = qdataAll.allSeries.seriesSummaries.map(s => s.id !== data.deletePhoto
            ? s
            : { ...s, photoCount: finalCount !== null ? finalCount : s.photoCount }
          )
          store.writeQuery({ query: allSeries, data: qdataAll })
          console.log('deletePhotoMutation qdataAll WRITE:', qdataAll)
        }
        catch (e) { }

        console.log('Done Manip:', qdata.series.photos)
      }
    })
  })
})

const withCreateNewSeries = graphql(createSeries, {
  props: ({ mutate, ownProps }) => {
    return {
      createSeriesMutation: (newSeries) => {
        // input NewSeriesInput {
        //   name: String!
        //   note: String
        //   latitude: Float!
        //   longitude: Float!
        //   direction: Int
        //   meta: String
        // }
        return mutate({
          variables: { series: sanitizeUpdateParams(newSeries, ['name', 'note', 'latitude', 'longitude', 'direction', 'meta']) },
          optimisticResponse: {
            'createSeries': {
              'id': localUUID('SERIES'),
              'name': newSeries.name || null,
              'note': newSeries.note || null,
              'meta': newSeries.meta || {},
              'featured': newSeries.featured || null,
              'coords': newSeries.coords || null,
              'direction': newSeries.direction || null,
              'photoCount': 0,
              'photos': [],
              __typename: 'Series'
            }
          },
          update: (store, { data }) => {
            // We need to write this query from scratch
            console.log('STARTING TO CREATE THE SERIES', data, store)
            // dumpLog(store.data, '##CACHE-NEW_SERIES_BEFORE')
            try {
              store.writeQuery({ query: getWholeSeries,
                data: { series: data.createSeries }
              })
              console.log('Series write getWholeSeries', { series: data.createSeries })
            }
            catch (e) {
              console.error(e)
            }

            try {
              const qdataAll = store.readQuery({ query: allSeries, variables: { limit: 100 } })
              // console.log('createSeriesMutation qdataAll:', qdataAll)
              const newAllSeriesEntry = sanitizeUpdateParams(data.createSeries, ['id', 'name', 'updatedOn', 'featured', 'longitude', 'latitude', 'photoCount', 'seriesStatus', 'direction'])
              qdataAll.allSeries.seriesSummaries.push(newAllSeriesEntry)
              store.writeQuery({ query: allSeries, data: qdataAll })
              // console.log('createSeriesMutation qdataAll WRITE:', qdataAll)
            }

            catch (e) {
              // dumpLog(store.data, '##CACHE-NEW_SERIES_ERROR')
              console.error('createSeriesMutation allSeries error')
              console.error(e)
            }
            // dumpLog(store.data, '##CACHE-NEW_SERIES_AFTER')
          }
        })
      }
    }
  }
})

const withCreateNewPhoto = graphql(createPhoto, {
  props: ({ mutate, ownProps }) => {
    return {
      createPhotoMutation: (newPhoto, ext) => {
        // input NewPhotoInput {
        //   seriesId: ID!
        //   takenOn: String
        //   meta: String
        //   tags: [TagInput]
        //   note: String
        // }
        // When we're not creating a series the seriesId isn't passed in
        // so we add it now
        const { base64, ...newPhotoInput } = newPhoto
        if (!newPhotoInput.seriesId && ownProps.activeSeries) {
          newPhotoInput.seriesId = ownProps.activeSeries
        }
        return mutate({
          variables: { photo: sanitizeUpdateParams(newPhotoInput, [ 'seriesId', 'takenOn', 'meta', 'tags', 'note' ]), ext },
          optimisticResponse: {
            'createPhoto': {
              'photo': {
                'id': localUUID('PHOTO'),
                'meta': newPhotoInput.meta,
                'seriesId': newPhotoInput.seriesId,
                'createdOn': Date.now().toString(),
                'createdUser': {
                  'id': ownProps.authData.attributes.sub,
                  'username': ownProps.authData.Username
                },
                'updatedBy': ownProps.authData.attributes.sub,
                'updatedOn': Date.now().toString(),
                'takenOn': newPhotoInput.takenOn,
                'tags': newPhotoInput.tags || [],
                'paths': {
                  large: null,
                  orig: null,
                  thumb: null,
                  medium: null,
                  __typename: 'Paths'
                },
                '__typename': 'Photo'
              },
              'preAuth': null,
              '__typename': 'NewPhotoOutput'
            }
          },
          update: (store, { data }) => {
            // Update: getWholeSeries
            console.log('createPhotoMutation store.readQuery:', { data, store, ownProps })

            try {
              const qdata = store.readQuery({ query: getWholeSeries, variables: { id: data.createPhoto.seriesId } })
              qdata.series.photos = qdata.series.photos = [...qdata.series.photos, data.createPhoto]

              store.writeQuery({ query: getWholeSeries, data: qdata })
            }
            catch (e) {
              // This won't fire on new series and that's ok for now.
            }

            // Update: allSeries
            // We might not have called this query yet so let it throw if it wants
            let qdataAll = null
            try {
              qdataAll = store.readQuery({ query: allSeries, variables: { limit: 100 } })
              console.log('createPhotoMutation qdataAll:', qdataAll)
              qdataAll.allSeries.seriesSummaries = qdataAll.allSeries.seriesSummaries.map(s => s.id !== data.createPhoto.photo.seriesId
                ? s
                : { ...s, photoCount: s.photoCount + 1 }
              )
              store.writeQuery({ query: allSeries, data: qdataAll })
              console.log('createPhotoMutation qdataAll: WRITE', qdataAll)
            }
            catch (e) {
              console.error('createPhotoMutation allSeries ERROR')
              console.error(e)
            }
          }
        })
      }
    }
  }
})

