import React from 'react'
import PropTypes from 'prop-types'

/**
 * Note: this was one of the few components we didn't use hooks for
 * mostly because I couldn't find an easy way to do getDerivedStateFromError()
 *
 * We might revise this later
 */
class ErrorBoundary extends React.Component {
  constructor (props) {
    super(props)
    this.state = { hasError: false }
  }

  static getDerivedStateFromError (error) {
    // Update state so the next render will show the fallback UI.
    console.error(error)
    return { hasError: true, error }
  }

  // componentDidCatch (error, info) {
  //   console.error(error)
  //   // You can also log the error to an error reporting service
  //   // logErrorToMyService(error, info)
  // }

  render () {
    const { hasError, error } = this.state
    const { fallback } = this.props
    const FallbackUI = fallback
    if (hasError) {
      // You can render any custom fallback UI
      return <FallbackUI error={error} />
    }

    return this.props.children
  }
}
ErrorBoundary.propTypes = {
  fallback: PropTypes.any,
  children: PropTypes.any
}

export default ErrorBoundary
