import React, { useState } from 'react'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'
import Divider from '@material-ui/core/Divider'

import SeriesListItem from '../misc/SeriesListItem'
import { makeStyles } from '@material-ui/core/styles'
import TopBar from '../TopBar'
import MeCard from './MeCard'

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    height: '100%'
  },
  filterContainer: {
    color: '#AAAAAA'
  },
  filterChip: {

  },
  seriesList: {
    height: '100%',
    overflowX: 'hidden',
    overflowY: 'scroll'
  }
}))

const FILTERS = {
  ALL: 'All',
  OWN: 'Own',
  CONTRIBUTED: 'Contributed',
  WATCHING: 'Watching'
}

const SORT = {
  ACTIVITY: 'Last Photo',
  CREATED: 'Created',
  NAME: 'Name'
}

const MyProfile = ({ user, feed, loading }) => {
  const classes = useStyles()

  const [filterVal, setFilter] = useState(FILTERS.ALL)
  const [sortVal, setSort] = useState(SORT.ACTIVITY)
  const [sortAsc, setSortAsc] = useState(true)

  const sortSetter = (newSort) => {
    if (newSort === sortVal) setSortAsc(!sortAsc)
    else setSort(newSort)
  }

  let filteredFeed = []

  switch (filterVal) {
    case FILTERS.ALL:
      filteredFeed = [...feed]
      break
    case FILTERS.OWN:
      filteredFeed = feed.filter(fi => fi.createdUser.id === user.id)
      break
    case FILTERS.CONTRIBUTED:
      filteredFeed = []
      break
    case FILTERS.WATCHING:
      filteredFeed = feed.filter(fi => user.watching.indexOf(fi.id) > -1)
      break
    default:
      break
  }

  switch (sortVal) {
    case SORT.ACTIVITY:
      if (sortAsc) filteredFeed.sort((a, b) => a.latest.takenOn - b.latest.takenOn)
      else filteredFeed.sort((a, b) => b.latest.takenOn - a.latest.takenOn)
      break
    case SORT.CREATED:
      if (sortAsc) filteredFeed.sort((a, b) => a.createdOn - b.createdOn)
      else filteredFeed.sort((a, b) => b.createdOn - a.createdOn)
      break
    case SORT.NAME:
      if (sortAsc) {
        filteredFeed.sort((a, b) => {
          if (a.name < b.name) {
            return -1
          }
          if (a.name > b.firstname) {
            return 1
          }
          return 0
        })
      }
      else {
        filteredFeed.sort((a, b) => {
          if (b.name < a.name) {
            return -1
          }
          if (b.name > a.name) {
            return 1
          }
          return 0
        })
      }
      break
    default:
      break
  }

  return (
    <div className={classes.root}>
      <TopBar>
        <Typography>My Profile</Typography>
      </TopBar>
      <MeCard user={user} />
      <Divider />
      <div className={classes.filterContainer}>
        <div>
          <Typography component='span'>Filter: </Typography>
          <FilterSortItem active={filterVal === FILTERS.ALL} onClick={() => setFilter(FILTERS.ALL)} label={FILTERS.ALL} />
        |
          <FilterSortItem active={filterVal === FILTERS.OWN} onClick={() => setFilter(FILTERS.OWN)} label={FILTERS.OWN} />
        |
          <FilterSortItem active={filterVal === FILTERS.CONTRIBUTED} onClick={() => setFilter(FILTERS.CONTRIBUTED)} label={FILTERS.CONTRIBUTED} />
        |
          <FilterSortItem active={filterVal === FILTERS.WATCHING} onClick={() => setFilter(FILTERS.WATCHING)} label={FILTERS.WATCHING} />

        </div>
        <div>
          <Typography component='span'>Sort: </Typography>
          <FilterSortItem asc={sortAsc} active={sortVal === SORT.NAME} onClick={() => sortSetter(SORT.NAME)} label={SORT.NAME} />
        |
          <FilterSortItem asc={sortAsc} active={sortVal === SORT.ACTIVITY} onClick={() => sortSetter(SORT.ACTIVITY)} label={SORT.ACTIVITY} />
        |
          <FilterSortItem asc={sortAsc} active={sortVal === SORT.CREATED} onClick={() => sortSetter(SORT.CREATED)} label={SORT.CREATED} />
        </div>
      </div>
      <div className={classes.seriesList}>
        {filteredFeed.map((series, idx) => (
          <SeriesListItem
            key={`seriesListItem${idx}`}
            series={series}
            showCreatedOn
            showLastPhoto
          />
        ))}
      </div>
    </div>
  )
}

MyProfile.propTypes = {
  user: PropTypes.object,
  loading: PropTypes.object.isRequired,
  feed: PropTypes.array.isRequired
}

export default MyProfile

const useFilterStyles = makeStyles(theme => ({
  root: {
    margin: theme.spacing(0, 1, 0, 1)
  }
}))

const FilterSortItem = ({ active, asc, onClick, label }) => {
  const classes = useFilterStyles()

  const arrow = active && typeof asc !== 'undefined'
    ? asc === true ? '▲' : '▼'
    : ' '

  return (
    <Typography
      className={classes.root}
      onClick={onClick}
      variant='caption'
      color={active ? 'textPrimary' : 'textSecondary'}
    >
      {label} {arrow}
    </Typography>
  )
}

FilterSortItem.propTypes = {
  active: PropTypes.bool,
  asc: PropTypes.bool,
  onClick: PropTypes.func.isRequired,
  label: PropTypes.string.isRequired
}
