import React, { useRef, useEffect, useState } from 'react'
import Typography from '@material-ui/core/Typography'
// import MenuIcon from '@material-ui/icons/Menu'
import { makeStyles } from '@material-ui/core/styles'
import useOnline from '../../lib/useOnline'
import moment from 'moment'

const CHECKINTERVAL = 5000

const useStyles = makeStyles(theme => ({
  root: {
    background: 'black',
    color: 'white',
    textAlign: 'center'
  }
}))

const OfflineBar = () => {
  const classes = useStyles()
  const { online, since } = useOnline()
  const [sinceStr, setSinceStr] = useState()
  const timerRef = useRef()

  const calcSinceStr = () => {
    const newText = moment(since, 'x').fromNow()
    // console.log('OFFLINE SINCE', since, newText)
    setSinceStr(newText)
  }
  const clearTimer = () => {
    if (timerRef.current) {
      clearInterval(timerRef.current)
      timerRef.current = null
    }
  }

  useEffect(() => {
    if (since && !timerRef.current) {
      calcSinceStr()
      timerRef.current = setInterval(calcSinceStr, CHECKINTERVAL)
    }
    else if (!since)clearTimer()
    return () => {
      clearTimer()
    }
  }, [since])

  if (online) return null
  return (
    <Typography className={classes.root} variant='caption' component='div'>
      Offline ({sinceStr})
    </Typography>
  )
}

export default OfflineBar
