import React, { useEffect, useRef } from 'react'

const useFileInput = (onChange = () => {}) => {
  const inputRef = useRef()
  const clickRef = useRef()

  const element = (
    <input style={{ display: 'none' }} ref={inputRef} type='file' />
  )

  const forceClick = () => {
    if (inputRef.current) inputRef.current.click()
  }

  useEffect(() => {
    const handleChange = evt => {
      const [file] = [...inputRef.current.files]
      if (file) {
        const reader = new FileReader()
        reader.onload = e => {
          onChange(e.target.result)
        }
        reader.readAsDataURL(file)
      }
      inputRef.current.value = null
    }

    if (inputRef.current) {
      inputRef.current.addEventListener('change', handleChange)
      if (!clickRef.current) {
        inputRef.current.click()
        clickRef.current = true
      }
    }
    return () => {
      if (inputRef.current) {
        inputRef.current.removeEventListener('change', handleChange)
      }
    }
  }, [inputRef.current])

  return { fileClickEl: element, forceClick }
}

export default useFileInput
