import React, { useContext, useEffect } from 'react'
import PropTypes from 'prop-types'

import CssBaseline from '@material-ui/core/CssBaseline'

import UploaderConnect from './graphql/Uploader.connect'
import SeriesConnect from './graphql/Series.connect'
import MapConnect from './graphql/Map.connect'
import SearchConnect from './graphql/Search.connect'
import ProfileConnect from './graphql/Profile.connect'
import FeedConnect from './graphql/Feed.connect'

import OfflineBar from './components/misc/OfflineBar'
import BottomBar from './components/BottomBar'
import { AppContext } from './contexts'

import { makeStyles } from '@material-ui/core/styles'
import { home as homeConfig } from './config'

const STATES = homeConfig.AppStateTypes

const useStyles = makeStyles(theme => ({
  root: {
    height: '100%',
    overflow: 'hidden',
    display: 'flex',
    flexDirection: 'column'
  },
  offlineBar: {
    flex: '0 0'
  },
  activeWrapper: {
    overflow: 'hidden',
    flexGrow: 1,
    flexShrink: 1
  },
  uploaderContainer: {
    zIndex: 2000,
    flex: '0 0'
  }
}))

/**
 * This is the over-arching wrapper for the app. It combines all the
 * same place. Time will tell if that's a good strategy.
 */
const App = ({ domain, setHomeState, onCameraClick, onLibraryClick }) => {
  const classes = useStyles()
  const { signedIn, isVKeyBdOn } = useContext(AppContext)

  useEffect(() => {
    // No home feed for anonymous users
    if (!signedIn && domain === STATES.FEED) setHomeState(STATES.MAP)
  }, [domain])

  let showBottomBar = true
  let ActiveComponent = FeedConnect
  switch (domain) {
    case STATES.FEED:
      ActiveComponent = FeedConnect
      break
    case STATES.MAP:
      ActiveComponent = MapConnect
      break
    case STATES.PROFILE:
      ActiveComponent = ProfileConnect
      break
    case STATES.SEARCH:
      ActiveComponent = SearchConnect
      break
    case STATES.SERIES:
      ActiveComponent = SeriesConnect
      showBottomBar = false
      break
    case STATES.NEW_SERIES_CAMERA:
      ActiveComponent = SeriesConnect
      showBottomBar = false
      break
    case STATES.NEW_SERIES_LIBRARY:
      ActiveComponent = SeriesConnect
      showBottomBar = false
      break
    default:
      break
  }

  return (
    <div className={classes.root}>
      <CssBaseline /> {/* You only need this in a root element. Don't put it anywhere else in the dom */}
      {!isVKeyBdOn && <OfflineBar className={classes.offlineBar} />}
      <div className={classes.activeWrapper}>
        <ActiveComponent />
      </div>
      {!isVKeyBdOn && showBottomBar && (
        <BottomBar
          signedIn={signedIn}
          onHomeClick={() => setHomeState(STATES.FEED)}
          onMapClick={() => setHomeState(STATES.MAP)}
          onSearchClick={() => setHomeState(STATES.SEARCH, { search: null })}
          onProfileClick={() => setHomeState(STATES.PROFILE)}
          onCameraClick={onCameraClick}
          onLibraryClick={onLibraryClick}
        />
      )}
      {/* Uploader is persistent across all views */}
      {!isVKeyBdOn && <UploaderConnect className={classes.uploaderContainer} />}
    </div>
  )
}

App.propTypes = {
  // All of this comes in from From Redux
  domain: PropTypes.string.isRequired,
  setHomeState: PropTypes.func.isRequired,
  onCameraClick: PropTypes.func.isRequired,
  onLibraryClick: PropTypes.func.isRequired
}

export default App
