import React from 'react'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'
import PhotoLibraryIcon from '@material-ui/icons/PhotoLibrary'
import { makeStyles } from '@material-ui/core/styles'
import useFileInput from '../../../../lib/useFileInput'

const color = '#AAAAAA'

const useStyles = makeStyles(theme => ({
  root: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
    textAlign: 'center',
    color,
    border: `20px solid ${color}`
  },
  icon: {
    margin: 'auto',
    flex: '1 1 50%',
    height: '70%',
    width: '70%'
  },
  text: {
    flex: '1 1 50%'
  }
}))

const LibraryFilePicker = ({ onLibraryAdd }) => {
  const classes = useStyles()
  const { fileClickEl, forceClick } = useFileInput(onLibraryAdd)
  return (
    <div className={classes.root} onClick={forceClick}>
      {fileClickEl}
      <PhotoLibraryIcon className={classes.icon}color='inherit' />
      <Typography variant='h2' className={classes.text} color='inherit'>
        Import from Library
      </Typography>
    </div>
  )
}

LibraryFilePicker.propTypes = {
  onLibraryAdd: PropTypes.func.isRequired
}

export default LibraryFilePicker
