import React from 'react'
import PropTypes from 'prop-types'
import { graphql, compose } from 'react-apollo'

import { getUser, userSeries, seriesFromList } from './queries'
import MyProfile from '../components/MyProfile/MyProfile'

const ProfileConnect = ({ data }) => {
  const { userQ, seriesQ, watchedSeriesQ } = data
  const user = userQ && userQ.user ? userQ.user : null

  const seriesQArr = seriesQ && seriesQ.userSeries && seriesQ.userSeries.seriesSummaries ? seriesQ.userSeries.seriesSummaries : []
  const watchedSeriesQArr = watchedSeriesQ && watchedSeriesQ.arraySeries ? watchedSeriesQ.arraySeries : []
  console.log('ProfileConnect', { seriesQArr, watchedSeriesQ, watchedSeriesQArr })
  const feed = [...seriesQArr, ...watchedSeriesQArr]
  const loading = {
    user: userQ.loading,
    list: Boolean(
      (seriesQ && seriesQ.loading) ||
      (watchedSeriesQ && watchedSeriesQ.loading))
  }

  return <MyProfile user={user} loading={loading} feed={feed}
  />
}

ProfileConnect.propTypes = {
  data: PropTypes.object // from graphql
}

// This one is really simple.
const withGetUserQuery = graphql(getUser, {
  props: ({ data, ownProps }) => {
    // console.log('ProfileConnect withGetUserQuery', { data, ownProps })
    return { data: { userQ: data } }
  }
})

const withuserSeriesQuery = graphql(userSeries, {
  skip: ({ data }) => !data.userQ.user,
  props: ({ data, ownProps }) => {
    // console.log('ProfileConnect withuserSeriesQuery', { data, ownProps })
    return { data: { ...ownProps.data, seriesQ: data } }
  }
})

// const withMyPhotosQuery = graphql(myPhotos)

const withMyWatchedSeriesQuery = graphql(seriesFromList, {
  props: ({ data, ownProps }) => {
    // console.log('ProfileConnect withMyWatchedSeriesQuery', { data, ownProps })
    return { data: { ...ownProps.data, watchedSeriesQ: data } }
  },
  skip: ({ data }) => {
    try {
      return data.userQ.user.watching.length < 1
    }
    catch (e) {
      return true
    }
  },
  options: ({ data }) => {
    return {
      variables: { seriesList: data.userQ.user.watching }
    }
  }
})

/**
 * Compose all our queries together
 */
export default compose(
  withGetUserQuery,
  withuserSeriesQuery,
  // withMyPhotosQuery,
  withMyWatchedSeriesQuery
)(ProfileConnect)
