import uuid from 'uuid/v4'
import { convertJSDatetoTimestamp } from './datelib'
import SeriesConfig from './SeriesConfig'

class Photo {
  constructor (seriesConfig, photo) {
    this.config = new SeriesConfig(seriesConfig)
    if (photo) this.photo = photo
    else {
      this.photo = this.newPhoto()
    }
  }

  setMeta = () => {
    this.meta = JSON.stringify(this.config.getPhotoMeta())
  }

  newPhoto = () => ({
    id: '_' + uuid(),
    takenOn: convertJSDatetoTimestamp(new Date()),
    meta: JSON.stringify(this.config.getPhotoMeta()),
    tags: []
  })
}

export default Photo
