import React from 'react'
import PropTypes from 'prop-types'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
// import MenuIcon from '@material-ui/icons/Menu'
import { makeStyles } from '@material-ui/core/styles'
import { formatDate } from '../../lib/datelib'

const useStyles = makeStyles(theme => ({
  root: {

  },
  gravatarImg: {
    display: 'block',
    marginRight: theme.spacing(2),
    width: '100%',
    maxWidth: '25%',
    height: 'auto'
  }
}))

const MeCard = ({ user, loading, onAvatarClick }) => {
  const classes = useStyles()

  if (loading || !user) return 'LOADING...'
  console.log('USER', user)

  return (
    <ListItem className={classes.root} onClick={onAvatarClick} disableGutters>
      <img alt='profile' className={classes.gravatarImg} src={user.gravatar} />
      <ListItemText
        primary={user.username}
        secondary={`Since ${formatDate(user.createdOn)}`}
      />
    </ListItem>
  )
}

MeCard.propTypes = {
  user: PropTypes.object,
  loading: PropTypes.bool,
  onAvatarClick: PropTypes.func.isRequired
}

export default MeCard
