import React, { useContext } from 'react'
import PropTypes from 'prop-types'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import List from '@material-ui/core/List'
import TinyChip from './TinyChip'
import Typography from '@material-ui/core/Typography'
import { makeStyles } from '@material-ui/core/styles'
import { formatDate } from '../../lib/datelib'

import { ACCESS } from '../../config'
import { AppContext } from '../../contexts'

const style = theme => ({
  root: {
    margin: 0,
    padding: theme.spacing(1)
  },
  chipContainer: {
    display: 'flex',
    width: '100%'
  },
  grow: { flexGrow: 1 },
  accessPUBLIC: {
    color: 'white',
    background: ACCESS.PUBLIC.color
  },
  accessPRIVATE: {
    color: 'white',
    background: ACCESS.PRIVATE.color
  },
  accessOPEN: {
    color: 'white',
    background: ACCESS.OPEN.color
  },
  img: {
    objectFit: 'cover',
    height: '100%',
    width: '100%'
  },
  text: {
    margin: 0,
    padding: theme.spacing(0, 1),
    alignSelf: 'flex-start'
  },
  titleText: {
    textOverflow: 'ellipsis',
    whiteSpace: 'nowrap',
    overflow: 'hidden'
  },
  dateContainer: {
    display: 'flex'
  },
  dateLabel: {
    fontWeight: 'bold',
    flex: '1 1 50px'
  },
  dateText: {

  },
  imgContainer: {
    flex: '0 0 25%',
    alignSelf: 'flex-start',
    height: '100%'
  }
})
const useStyles = makeStyles(style)

const SeriesListItem = ({ series, loading, onClick, children, showCreatedOn, showLastPhoto }) => {
  const classes = useStyles()
  const { seriesConfig } = useContext(AppContext)
  if (loading || !series) return 'LOADING...'
  let thumb = null
  let accessLabel = ACCESS.PRIVATE.label
  try {
    thumb = seriesConfig.getPhotoUrl(series.featured, 'thumb')
    accessLabel = series.access ? ACCESS[series.access].label : ACCESS.PRIVATE.label
  }
  catch (e) {
    console.error(e)
  }
  return (
    <ListItem className={classes.root} button component='div' disableGutters>
      <div className={classes.imgContainer} >
        <img alt='thumbnail' src={thumb} className={classes.img} onClick={onClick} />
      </div>
      <ListItemText className={classes.text} disableTypography onClick={onClick}>
        <Typography variant='body1' component='div' className={classes.titleText}>
          {series.name || loading}
        </Typography>
        <Typography variant='body2' component='div'>
          {series.access && <TinyChip label={accessLabel} className={classes[`access${series.access}`]} />}
          Series <strong>by:</strong> {series.createdUser.username}
        </Typography>
        <Typography variant='caption' component='div'>{`${series.photoCount || 0} Photos`}</Typography>

        {showCreatedOn && <div className={classes.dateContainer}>
          <Typography variant='caption' className={classes.dateLabel}>Created:</Typography>
          <Typography variant='caption' className={classes.dateText}>{formatDate(series.createdOn, 'llll')}</Typography>
        </div>}
        {showLastPhoto && <div className={classes.dateContainer}>
          <Typography variant='caption' className={classes.dateLabel}>Last Photo:</Typography>
          <Typography variant='caption' className={classes.dateText}>{formatDate(series.latest.takenOn, 'llll')}</Typography>
        </div>}
      </ListItemText>
      {/*  <TinyChip label='Watching' color='secondary' /> */}
      {children}
    </ListItem>
  )
}

SeriesListItem.propTypes = {
  series: PropTypes.object,
  loading: PropTypes.bool,
  showCreatedOn: PropTypes.bool,
  showLastPhoto: PropTypes.bool,
  onClick: PropTypes.func,
  children: PropTypes.array
}

export default SeriesListItem

// const useCosmosStyles = makeStyles(theme => ({
//   root: {

//   }
// }))

export const SeriesListItemCosmos = (props) => {
  // const classes = useCosmosStyles()
  return (
    <List disablePadding dense>
      <SeriesListItem {...props} />
    </List>
  )
}
