import { useState, useEffect, useRef } from 'react'

export const useCamera = (onCapture) => {
  const [cameraError, setCameraError] = useState(false)
  const [cameraOnline, setCameraOnline] = useState(false)
  const camRef = useRef()

  const initCamera = () => {
    if (!camRef.current) {
      camRef.current = new Webcam(
        document.getElementById('webcam'),
        document.createElement('canvas')
      )
      camRef.current.setup(
        window.innerWidth, // width
        window.innerHeight // height
      )
        .then((data) => {
          setCameraOnline(true)
        })
        .catch((e) => {
          console.error(e)
          setCameraError(true)
        })
    }
  }

  const captureImage = async () => {
    if (camRef.current && cameraOnline) {
      const capturedData = camRef.current.takeBase64Photo({ type: 'jpeg', quality: 1 })
      onCapture(capturedData)
    }
  }

  const delayedOrientationChange = () => {
    window.setTimeout(initCamera, 1000)
  }

  useEffect(() => {
    window.addEventListener('orientationchange', delayedOrientationChange)
    if (!camRef.current && !cameraError) initCamera()
    return () => {
      window.removeEventListener('orientationchange', delayedOrientationChange)
      if (camRef.current) {
        camRef.current.destroy()
        setCameraError(false)
        setCameraOnline(false)
      }
      camRef.current = null
    }
  }, [])

  return { cameraError, captureImage, cameraOnline }
}

class Webcam {
  constructor (webcamElement, canvasElement) {
    this.webcamElement = webcamElement
    this.canvasElement = canvasElement

    if (window.location.protocol !== 'https:' && window.location.hostname !== 'localhost') {
      console.warn('getUserMedia() must be run from a secure origin: https or localhost.\nChanging protocol to https.')
    }

    if (!navigator.mediaDevices && !navigator.getUserMedia) {
      console.warn(`Your browser doesn't support navigator.mediaDevices.getUserMedia and navigator.getUserMedia.`)
    }
  }

  adjustVideoSize (width, height) {
    const aspectRatio = width / height
    if (width >= height) {
      this.webcamElement.width = aspectRatio * this.webcamElement.height
    }
    else if (width < height) {
      this.webcamElement.height = this.webcamElement.width / aspectRatio
    }
  }

  async destroy () {
    try {
      this.webcamElement.srcObject.getTracks()[0].stop()
      this.webcamElement = null
      this.canvasElement = null
    }
    catch (e) {
      console.error(e)
    }
  }

  async setup (width, height) {
    return new Promise((resolve, reject) => {
      if (navigator.mediaDevices.getUserMedia !== undefined) {
        navigator.mediaDevices.getUserMedia({ audio: false, video: { facingMode: 'environment', width, height } })
          .then((mediaStream) => {
            if ('srcObject' in this.webcamElement) {
              this.webcamElement.srcObject = mediaStream
            }
            else {
              // For older browsers withouth the srcObject.
              this.webcamElement.src = window.URL.createObjectURL(mediaStream)
            }
            this.webcamElement.onloadedmetadata = (e) => {
              this.webcamElement.play()
            }
            this.webcamElement.onplaying = (e) => {
              resolve()
            }
          })
          .catch(err => reject(err))
      }
      else {
        reject(new Error('Error accessing camera'))
      }
    })
  }

  _drawImage () {
    const imageWidth = this.webcamElement.videoWidth
    const imageHeight = this.webcamElement.videoHeight

    const context = this.canvasElement.getContext('2d')
    this.canvasElement.width = imageWidth
    this.canvasElement.height = imageHeight

    context.drawImage(this.webcamElement, 0, 0, imageWidth, imageHeight)

    return { imageHeight, imageWidth }
  }

  takeBlobPhoto () {
    const { imageWidth, imageHeight } = this._drawImage()
    return new Promise((resolve, reject) => {
      this.canvasElement.toBlob((blob) => {
        resolve({ blob, imageHeight, imageWidth })
      })
    })
  }

  takeBase64Photo ({ type, quality } = { type: 'jpeg', quality: 1 }) {
    const { imageHeight, imageWidth } = this._drawImage()
    const base64 = this.canvasElement.toDataURL('image/' + type, quality)
    return { base64, imageHeight, imageWidth }
  }
}
