import React, { useState, useRef, useContext, useEffect } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'

import { makeStyles } from '@material-ui/core/styles'

import useComponentSize from '../../../../lib/useComponentSize'

import { SeriesContext } from '../../contexts'
import XYTagChip from '../tags/XYTagChip'
import useXYTags from '../tags/useXYTags'
import BrokenImg from './BrokenImg'
import style from './Photo.style'

const useStyles = makeStyles(style)

const Photo = ({ photo, opacity, edit, showTags, transition, onChange, className }) => {
  const classes = useStyles()
  const refImgWrap = useRef(null)
  const [newSize, setNewSize] = useState({ multiplier: 1 })
  const [url, setUrl] = useState(null)
  const [broken, setBroken] = useState(false)
  const [urlRetryCounter, setURLRetryCounter] = useState(0)
  const { seriesConfig } = useContext(SeriesContext)

  useEffect(() => {
    function getURL () {
      if (!photo.base64) {
        const newUrl = seriesConfig.getPhotoUrl(photo, 'large')
        if (refImgWrap.current) setUrl(newUrl)
      }
      else {
        if (refImgWrap.current) setUrl(photo.base64)
      }
    }
    getURL()
  }, [photo])

  const { size: { height, width }, setResizeActive } = useComponentSize(refImgWrap)
  const urlWithRetries = urlRetryCounter > 0 ? `${url}?retry=${urlRetryCounter}` : url

  // All tag-related info is put in its own hook
  const { onAddTagTrigger, TagChooser, getRelXYTags, onTagDragOver, onTagDragStop, onTagDelete } = useXYTags(photo, { height, width }, onChange, setResizeActive)

  const handleImageLoaded = event => {
    if (refImgWrap.current) {
      setBroken(false)
      const { naturalHeight, naturalWidth } = event.target
      // 4 different cases of orientation:
      const multiplier = Math.min(
        height / naturalHeight,
        width / naturalWidth
      )

      const newWidth = multiplier * naturalWidth
      const newHeight = multiplier * naturalHeight
      setNewSize({ newWidth, newHeight, naturalWidth, naturalHeight, multiplier })
    }
  }

  return (
    <div className={classNames(classes.root, className)}
      style={{ opacity: opacity !== null ? opacity : 1 }}
      ref={refImgWrap}
    >
      <div className={classes.inner}
        style={{
          height: newSize ? newSize.newHeight : 1,
          width: newSize ? newSize.newWidth : 1
        }}
        onClick={onAddTagTrigger}
      >
        { showTags || edit
          ? (
            <div className={className}>
              { getRelXYTags(newSize.multiplier).map((tag, idx) => (
                <XYTagChip
                  key={`xytag-${idx}`}
                  color={tag.color}
                  {...{ edit, newSize, tag, onTagDragOver, onTagDragStop, onTagDelete }} />
              ))}
            </div>
          ) : null
        }
        {broken && <BrokenImg />}
        <img className={classNames(broken ? classes.brokenImg : classes.img)}
          alt={urlWithRetries}
          onError={(err) => {
            if (urlRetryCounter < 10) {
              setBroken(true)
              console.log('Image Retry', urlRetryCounter)
              console.error(err)
              setTimeout(() => {
                if (refImgWrap.current) setURLRetryCounter(urlRetryCounter + 1)
              }, 4000)
            }
          }}
          src={urlWithRetries}
          onLoad={handleImageLoaded}
          style={{ height: newSize.newHeight, width: newSize.newWidth }}
        />
      </div>
      {edit && <TagChooser />}
    </div>
  )
}

Photo.propTypes = {
  className: PropTypes.string,
  opacity: PropTypes.number,
  photo: PropTypes.object.isRequired,
  edit: PropTypes.bool,
  showTags: PropTypes.bool,
  transition: PropTypes.string,
  onChange: PropTypes.func
}

export default Photo
