import gql from 'graphql-tag'

export const allSeries = gql`
  query allSeries($limit: Int, $nextToken: String) {
    allSeries(limit: $limit, nextToken: $nextToken) {
      seriesSummaries {
        id
        name
        access
        createdUser {
          id
          gravatar
          gravatarProfile
          username        
        }
        updatedOn
        createdOn
        photoCount
        coords
        direction
        watchers
        featured {
          id
          takenOn
          paths {
            base
            thumb
            medium
            large
          }
        }
        latest {
          id
          takenOn
          paths {
            base
            thumb
            medium
            large
          }
        }
      }
      nextToken
    }
  }
`

