export const getUser = gql`
  query getUser($userId: ID) {
    user(id: $userId){
      id
      username
      gravatar
      gravatarProfile
      createdOn
      updatedOn
      createdOn
      watching
    }
  }
`

export const geocode = gql`
  query geocode($search: String!) {
    geocoder(search: $search) {
      relevance
      text
      placeName
      bbox
      center
      # geoType
      # context
    }
  }
`
export const getMyFeed = gql`
  query myFeed($start: String, $end: String, $limit: Int) {
    myFeed(start: $start, end: $end, limit: $limit) {
      feed {
        series {
          id
          name
          createdUser {
            id
            gravatar
            username
          }
          updatedOn
          createdOn
          access
          coords
          watchers
          photoCount
        }
        photo {
          id
          updatedOn
          createdOn
          createdUser {
            id
            gravatar
            gravatarProfile
            username
          }
          paths {
            base
            thumb
            medium
          }
        }
        relation
      }
      window
    }
  }
`

export const userSearch = gql`
  query userSearch($searchStr: String!, $limit: Int) {
    userSearch(searchStr: $searchStr, limit: $limit ){
      id
      username
      gravatar
      gravatarProfile
      createdOn
      updatedOn
      createdOn
    }
  }
`

export const getWholeSeries = gql`
query getWholeSeries($id: ID!) {
  series(id: $id) {
    id
    name
    note
    meta
    watchers
    createdOn
    createdUser {
      id
      gravatar
      gravatarProfile
      username
    }
    updatedOn
    createdOn
    coords
    direction
    photoCount
    featured {
      id
      takenOn
      paths {
        base
        large
        thumb
        medium
      }
    }
    latest {
      id
      paths {
        base
        large
        thumb
        medium
      }
      takenOn
    }
    photos {
      id
      meta
      createdOn
      takenOn
      updatedOn
      createdOn
      createdUser {
        id
        gravatar
        gravatarProfile
        username
      }
      tags {
        label
        typeId
        coords {
          x
          y
        }
      }
      paths {
        base
        large
        thumb
        medium
      }
    }
  }
}
`
