import React, { useState, useContext } from 'react'
import PropTypes from 'prop-types'
import exifParser from 'exif-parser'
import { isNumeric } from '../../../lib/util'

import PhotoReviewTool from './PhotoReviewTool'
import CameraTool from './CameraTool'
import NewPhotoInfo from './NewPhotoInfo'
import LibraryFilePicker from './controls/LibraryFilePicker'

import PhotoClass from '../../../lib/Photo'
import SeriesClass from '../../../lib/Series'
import { useDeviceOrientation, useGeolocation } from '../../../lib/sensors'

import { SeriesContext } from '../contexts'

const MODES = {
  CAMERA: 'camera',
  FILEPICKER: 'library',
  PHOTOREVIEW: 'photoreview',
  PHOTOINFOEDIT: 'photoinfoedit'
}

const NewPhoto = ({ inputMethod, onCreate, onCancel, isNewSeries }) => {
  const { seriesConfig, series } = useContext(SeriesContext)
  const [newPhoto, setNewPhoto] = useState(null)
  const [newSeries, setNewSeries] = useState(isNewSeries ? new SeriesClass(seriesConfig).series : null)

  const [photoRefId, setPhotoRefId] = useState(null)
  const [mode, _changeMode] = useState({ current: inputMethod, history: [] })
  const orientation = useDeviceOrientation()
  const geo = useGeolocation()

  const photos = series && series.photos ? series.photos : []

  const onLibraryAdd = (data) => {
    console.log('onLibraryAdd Debug GOT DATA', { data })
    const find = 'base64,'
    let exif = {}
    const foundIdx = data.indexOf(find) + find.length
    const newData = data.slice(foundIdx)
    const parser = exifParser.create(Buffer.from(newData, 'base64'))
    console.log('onLibraryAdd Buffered')
    try {
      exif = parser.parse()
      console.log('got EXIF', JSON.stringify(exif))
    }
    catch (err) {
    // got invalid data, handle error
      console.error('EXIF ERROR', err)
    }
    // Ok, now try to get the datetime off the EXIF
    let date = String(Date.now())
    try {
      date = String(exif.tags.DateTimeOriginal)
    }
    catch (err) {
      console.log('EXIF', exif)
      console.error('ERROR GETTING TIMESTAMP', err)
    }
    console.log('isNewSeries', { isNewSeries })
    if (isNewSeries) {
      let { latitude, longitude } = geo
      // Stanley park as a fallback if we can't get the geo
      if (!latitude || !longitude) {
        latitude = 49.3050394
        longitude = -123.1398254
      }
      try {
        if (exif.tags.GPSLatitude && isNumeric(exif.tags.GPSLatitude) && exif.tags.GPSLongitude && isNumeric(exif.tags.GPSLongitude)) {
          latitude = exif.tags.GPSLatitude
          longitude = exif.tags.GPSLongitude
        }
      }
      catch (e) {
        console.error('GPS Longitude and Latitude could not be determined')
      }
      console.log('LATLONG FOR LIB PICK', { geo, latitude, longitude })
      setNewSeries({ ...newSeries, latitude, longitude })
    }
    const photoInitObj = new PhotoClass(seriesConfig).photo
    const fullNewPhotoObject = {
      ...photoInitObj,
      takenOn: date,
      rawData: { exif },
      base64: data
    }
    setNewPhoto(fullNewPhotoObject)
    changeMode(MODES.PHOTOREVIEW)
    console.log('onLibraryAdd FINISHED', fullNewPhotoObject)
  }

  const changeMode = (newModeName) => {
    console.log('NEWPHOTO changeMode', mode, newModeName)
    _changeMode({
      current: newModeName,
      history: [...mode.history, mode.current]
    })
  }
  const lastMode = () => {
    if (mode.history.length > 0) {
      const oldHistory = [...mode.history]
      const oldMode = oldHistory.pop()
      _changeMode({
        current: oldMode,
        history: oldHistory
      })
    }
    else {
      onCancel()
    }
  }

  const onShutter = (data, refId) => {
    const photoInitObj = new PhotoClass(seriesConfig).photo

    console.log('onShutter Debug GOT DATA', { data })
    const find = 'base64,'
    let exif = {}
    const foundIdx = data.base64.indexOf(find) + find.length
    const newData = data.base64.slice(foundIdx)
    const parser = exifParser.create(Buffer.from(newData, 'base64'))
    console.log('onLibraryAdd Buffered')
    try {
      exif = parser.parse()
      console.log('got EXIF', JSON.stringify(exif))
    }
    catch (err) {
    // got invalid data, handle error
      console.error('EXIF ERROR', err)
    }

    if (isNewSeries) {
      let { latitude, longitude } = geo
      // Stanley park as a fallback if we can't get the geo
      if (!latitude || !longitude) {
        latitude = 49.3050394
        longitude = -123.1398254
      }
      setNewSeries({ ...newSeries, latitude, longitude })
    }
    setNewPhoto({
      ...photoInitObj,
      rawData: {
        orient: orientation,
        geolocate: geo
      },
      base64: data.base64
    })
    setPhotoRefId(refId)
    changeMode(MODES.PHOTOREVIEW)
  }

  let component = null
  switch (mode.current) {
    case MODES.CAMERA:
      component = (
        <CameraTool
          photos={photos}
          onShutter={onShutter}
          onCancel={onCancel}
        />
      )
      break
    case MODES.FILEPICKER:
      component = <LibraryFilePicker onLibraryAdd={onLibraryAdd} />
      // fileClick() // move this to useEffect to help with click handling
      break
    case MODES.PHOTOREVIEW:
      component = (
        <PhotoReviewTool
          defaultRefPhotoId={photoRefId}
          photos={photos}
          captured={newPhoto}
          onConfirm={() => changeMode(MODES.PHOTOINFOEDIT)}
          onCancel={onCancel}
          onRetry={lastMode}
        />
      )
      break
    case MODES.PHOTOINFOEDIT:
      component = (
        <NewPhotoInfo
          photo={newPhoto}
          newSeries={newSeries}
          onSave={onCreate}
          onCancel={onCancel}
        />
      )
      break
    default:
      console.log('NEWPHOTO: default state. you should NEVER see this', mode)
      onCancel()
  }

  return component
}

NewPhoto.propTypes = {
  inputMethod: PropTypes.oneOf(['camera', 'library']),
  onCreate: PropTypes.func.isRequired,
  onCancel: PropTypes.func.isRequired,
  isNewSeries: PropTypes.bool,
  seriesConfig: PropTypes.object,
  // This might be a new series so we might have
  // actions to work on that
  // This will be provided EITHER photos or onSeriesCreate
  photos: PropTypes.array
}

export default NewPhoto
