import React, { useState, useContext } from 'react'
import classNames from 'classnames'
import PropTypes from 'prop-types'

import Toolbar from '@material-ui/core/Toolbar'
import Drawer from '@material-ui/core/Drawer'
import Divider from '@material-ui/core/Divider'
import IconButton from '@material-ui/core/IconButton'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import MenuIcon from '@material-ui/icons/Menu'
import AppLogo from '../icons/AppLogo'
import { AppContext } from '../contexts'

import ExitToAppIcon from '@material-ui/icons/ExitToApp'
import AccountCircleIcon from '@material-ui/icons/AccountCircle'

// import MenuIcon from '@material-ui/core/Menu'
import { makeStyles } from '@material-ui/core/styles'

const style = theme => ({
  root: {
    padding: '2px 4px',
    display: 'flex',
    alignItems: 'center',
    width: '100%',
    background: theme.palette.primary.main,
    color: theme.palette.primary.contrastText
  },
  searchIcon: {
    opacity: 0.3,
    marginRight: theme.spacing(1)
  },
  transparent: {
    background: 'none',
    color: theme.palette.text.primary
  },
  input: {
    background: '#FFFFFF',
    border: '1px solid #AAAAAA',
    borderRadius: theme.spacing(1) * 2,
    paddingLeft: theme.spacing(1),
    marginLeft: 0,
    flex: 1
  },
  children: {
    flexGrow: 1
  },
  iconButton: {
    padding: 10
  },
  divider: {
    width: 1,
    height: 28,
    margin: 4
  }
})
const useStyles = makeStyles(style)

const TopBar = ({ className, children, isTransparent, menuItems }) => {
  const classes = useStyles()
  const [drawerOpen, setDrawerOpen] = useState(false)
  const { signOut, signedIn, forceSignIn } = useContext(AppContext)
  return (
    <Toolbar className={classNames(classes.root, className, isTransparent ? classes.transparent : null)} disableGutters>
      <IconButton className={classes.iconButton}>
        <AppLogo />
      </IconButton>
      {/* {hasSearch && (
        <InputBase
          className={classes.input}
          placeholder='Search...'
          endAdornment={
            <InputAdornment position='end'>
              <SearchIcon className={classes.searchIcon} />
            </InputAdornment>
          }
        />
      )} */}
      <div className={classes.children}>
        {children}
      </div>
      {signedIn && (
        <IconButton
          color='inherit'
          className={classes.iconButton}
          aria-label='Menu'
          onClick={() => setDrawerOpen(!drawerOpen)}>
          <MenuIcon color='inherit' />
        </IconButton>
      )}
      {!signedIn && (
        <IconButton
          color='primary'
          className={classes.iconButton}
          aria-label='Menu'
          onClick={forceSignIn}
        >
          <AccountCircleIcon color='primary' />
        </IconButton>
      )}
      <Drawer anchor='top' open={drawerOpen} onClose={() => setDrawerOpen(false)}>
        <div
          tabIndex={0}
          role='button'
          onClick={() => setDrawerOpen(false)}
          onKeyDown={() => setDrawerOpen(false)}
        >
          <div className={classes.fullList}>
            <List>
              {menuItems && menuItems.map(({ callback, text, icon, color }, idx) => (
                <ListItem button key={`top-menu-item-${idx}`} onClick={callback}>
                  {icon && <ListItemIcon color={color}>{icon}</ListItemIcon>}
                  <ListItemText primary={text} color={color} />
                </ListItem>
              ))}
            </List>
            {menuItems && <Divider />}
            <List>
              {signedIn && (
                <ListItem button onClick={signOut}>
                  <ListItemIcon>
                    <ExitToAppIcon />
                  </ListItemIcon>
                  <ListItemText primary='Sign Out' />
                </ListItem>
              )}
            </List>
          </div>

        </div>
      </Drawer>
    </Toolbar>
  )
}

TopBar.propTypes = {
  className: PropTypes.string,
  children: PropTypes.any,
  isTransparent: PropTypes.bool,
  menuItems: PropTypes.arrayOf(PropTypes.shape({
    callback: PropTypes.func,
    text: PropTypes.string,
    icon: PropTypes.any,
    color: PropTypes.string
  }))
}

export default TopBar
