import React from 'react'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import CssBaseline from '@material-ui/core/CssBaseline'
import BugReportIcon from '@material-ui/icons/BugReport'
import ErrorIcon from '@material-ui/icons/Error'
import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles(theme => ({
  root: {
    height: '100%',
    overflow: 'hidden',
    padding: theme.spacing(1),
    display: 'flex',
    flexDirection: 'column',
    textAlign: 'center',
    color: theme.palette.text.primary,
    border: `20px solid ${theme.palette.text.secondary}`
  },
  icon: {
    margin: 'auto',
    flex: '1 1 20%',
    height: '70%',
    width: '70%'
  },
  text: {
  },
  stack: {
    padding: theme.spacing(2),
    marginBottom: theme.spacing(2),
    fontSize: '8px',
    flexGrow: 1,
    textAlign: 'left',
    whiteSpace: 'pre-wrap',
    overflow: 'scroll',
    color: theme.palette.error.main,
    fontFamily: 'Consolas, monaco, monospace'
  }
}))

const ErrorScreen = ({ error }) => {
  const classes = useStyles()

  let stackEl = null
  try {
    if (process.env.NODE_ENV === 'development') {
      stackEl = (
        <Typography className={classes.stack}>
          {error.stack}
        </Typography>
      )
    }
  }
  catch (e) {}

  return (
    <div className={classes.root}>
      <CssBaseline />
      <ErrorIcon className={classes.icon}color='inherit' />
      <Typography variant='subtitle1' className={classes.text} color='inherit'>
        Something went wrong
      </Typography>
      {error && error.message && <Typography variant='body1' className={classes.text} color='inherit'>
        {error.message}
      </Typography>}
      {stackEl}
      <Button variant='outlined' disabled>
        <BugReportIcon />
        File a bug report
      </Button>
    </div>
  )
}

ErrorScreen.propTypes = {
  error: PropTypes.object
}

export default ErrorScreen
