import React, { useContext } from 'react'
import PropTypes from 'prop-types'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'
// import MenuIcon from '@material-ui/icons/Menu'
import { makeStyles } from '@material-ui/core/styles'
import FindOnMapButton from '../misc/FindOnMapButton'
// import StarIcon from '@material-ui/icons/Star'
// import StarBorderIcon from '@material-ui/icons/StarBorder'
import { Avatar } from '@material-ui/core'
import { AppContext } from '../../contexts'
import { formatDate } from '../../lib/datelib'

const useStyles = makeStyles(theme => ({
  root: {
    height: '100%',
    padding: theme.spacing(0, 0.5, 1, 0.5)
  },
  imgContainer: {
    overflow: 'hidden',
    height: '100%',
    position: 'relative'
  },
  findOnMapButtonContainer: {
    position: 'absolute',
    opacity: 0.8,
    bottom: theme.spacing(1),
    right: theme.spacing(1)
  },
  titleContainer: {
    background: '#444444AA',
    color: 'white',
    position: 'absolute',
    alignItems: 'center',
    display: 'flex',
    top: 0,
    width: '100%',
    paddingRight: theme.spacing(1)
  },
  avatar: {
    border: '1px solid white',
    margin: theme.spacing(0, 1)
  },
  title: {
    flexGrow: 1
  },
  img: {
    objectFit: 'cover',
    height: '100%',
    width: '100%'
  }
  // gravatarImg: {
  // }
}))

const EVENT_VERBS = {
  DELETE: 'Deleted',
  ADD: 'Added'
}

const FeedItem = ({ event, onMapClick, onImageClick, onProfileClick, onSeriesClick }) => {
  const { series, photo } = event
  // console.log('EVENT', { series, relation, photo })
  const classes = useStyles()
  const { seriesConfig } = useContext(AppContext)

  // const isDelete = event.verb === 'ADD'
  // const seriesWatching = user.watching && user.watching.indexOf(series.id) > -1

  return (
    <Paper className={classes.root} elevation={1}>
      <div className={classes.imgContainer}>
        {/* <div className={classes.imgContainer}> */}
        <img
          alt='medium'
          className={classes.img}
          src={seriesConfig.getPhotoUrl(photo, 'medium')}
          onClick={() => onImageClick(series.id, photo.id)}
        />
        {/* </div> */}
        {/* <div className={classes.findOnMapButtonContainer}>
          <FindOnMapButton onClick={() => onMapClick(series)} />
        </div> */}
        <div className={classes.titleContainer}>
          <Avatar onClick={() => onProfileClick(photo.createdUser.username)} className={classes.avatar}>
            <img alt='profile' className={classes.gravatarImg} src={photo.createdUser.gravatar} />
          </Avatar>
          <div className={classes.title}>
            <Typography variant='body1' color='inherit'>{series.name}</Typography>
            <Typography variant='body2' color='inherit'>{photo.createdUser.username} {EVENT_VERBS.ADD} a photo</Typography>
            <Typography variant='caption' color='inherit'>{formatDate(photo.updatedOn)}</Typography>
          </div>
          {/* <IconButton color='inherit' className={classes.watchButton} >
            {seriesWatching ? <StarIcon color='inherit' /> : <StarBorderIcon color='inherit' />}
          </IconButton> */}
          <FindOnMapButton onClick={() => onMapClick(series.id, photo.id)} />
        </div>
      </div>
    </Paper>
  )
}

FeedItem.propTypes = {
  // TODO: THIS IS NOT THE SHAPE WE WANT. NEED TO HASH THIS OUT
  event: PropTypes.object.isRequired,
  onSeriesClick: PropTypes.func.isRequired,
  onImageClick: PropTypes.func.isRequired,
  onProfileClick: PropTypes.func.isRequired,
  onMapClick: PropTypes.func.isRequired
}

export default FeedItem

