import React, { useMemo } from 'react'
import { Query, ApolloProvider, compose } from 'react-apollo'
import { IntlWrapper } from './translations'
import { getUser } from './graphql/queries'
import { AppContext } from './contexts'
import Loading from './components/auth/Loading'
import { createApolloClient } from './lib/useGraphQL'
import ErrorBoundary from './lib/ErrorBoundary'
import useAuthentication from './lib/useAuthentication'
import useShowAddToHome from './lib/useShowAddToHome'
import useResponsive from './lib/useResponsive'
import withReduxProvider from './redux/withReduxProvider'
import { connect } from 'react-redux'
import actions from './redux/actions'
import config, { home as homeConfig } from './config'

import App from './App'
import ErrorScreen from './components/misc/ErrorScreen'

/**
 * HOC for AppContext and ApolloProvider
 */
export const withApolloAuthProvider = Wrapped => (props) => {
  // console.log('withApolloAuthProvider', props)
  const { signedIn, isGod, authData, signOut, authScreen, accessToken, forceSignIn } = useAuthentication()
  const apolloClient = useMemo(() => createApolloClient(accessToken), [accessToken])

  // This triggers all the PWA Goodness
  useShowAddToHome()
  const responsive = useResponsive()

  // If the user hasn't decided to be anonymous or sign in yet show them the login screen
  console.log('Rendering AppConnect', { responsive, props, signedIn, isGod, authData })
  if (signedIn === null) return authScreen

  // These are the things the rest of the app needs to find:
  const appContext = {
    signOut,
    forceSignIn,
    ...responsive,
    signedIn,
    role: !signedIn
      ? 'anonymous'
      : isGod
        ? 'admin'
        : 'user',
    currentUser: authData, // TODO: Need this supported by a graphql query
    seriesConfig: {
      meta: {},
      tags: [
        {
          id: 'freeBothType',
          label: null,
          color: config.tagColors[0],
          allowXY: true,
          allowNonXY: true,
          allowAdditions: true,
          // This one returns a promise
          getValues: () => Promise.resolve(['a', 'b', 'c']),
          values: []
        }
      ],
      getPhotoUrl: (photo, size) => {
        try {
          return `${photo.paths.base}${photo.paths[size]}`
        }
        catch (e) {
          return null
        }
      }
    }
  }

  return (
    // We wrap the outside in an error handler
    <ErrorBoundary fallback={ErrorScreen}>
      <ApolloProvider client={apolloClient}>
        <Query query={getUser} skip={!signedIn}>
          {({ loading, error, data }) => {
            if (loading) return <Loading />
            if (error) {
              console.error(error)
            }
            if (signedIn) appContext.user = data.user
            return (
              <AppContext.Provider value={appContext}>
                <IntlWrapper>
                  <Wrapped {...props} />
                </IntlWrapper>
              </AppContext.Provider>
            )
          }}
        </Query>
      </ApolloProvider>
    </ErrorBoundary>
  )
}

/**
 * Map our redux state and functions
 */
const mapStateToProps = (state, props) => {
  const { domain } = state.home.current
  return { domain }
}

const mapDispatchToProps = dispatch => ({
  setHomeState: (newDomain, newParams) => {
    dispatch(actions.setHomeState(newDomain, newParams))
  },
  onCameraClick: () => {
    dispatch(actions.setHomeState(homeConfig.AppStateTypes.NEW_SERIES_CAMERA))
  },
  onLibraryClick: () => {
    dispatch(actions.setHomeState(homeConfig.AppStateTypes.NEW_SERIES_LIBRARY))
  }
})

/**
 * Nearly everything we do depends on who you are as a user so we commit this to state
 */

// The order we compose things here matters a lot
const withAppState = connect(
  mapStateToProps,
  mapDispatchToProps
)

export default compose(
  // ORDER IS CRITICAL HERE
  withReduxProvider,
  withApolloAuthProvider,
  withAppState
)(App)
