import { openDb } from 'idb'
import { localDb } from '../config'
import uuidv4 from 'uuid/v4'

/**
 * This is a leftover from the original, static RepeatablePhotos app.
 * For now it's just a decent way of storing the image data
 */
const dbPromise = openDb('serieslistDB', 1, upgradeDB => {
  console.log('UPGRADE NEEDED', upgradeDB)
  const store = upgradeDB.createObjectStore(localDb.imgStore)
  console.log('UPGRADE NEEDED STORE', store)
}).catch(e => {
  console.error('ERROR', e)
})

export const localUUID = (prefix) => {
  return `__LOCAL_${prefix}::${uuidv4()}`
}

export async function get (key) {
  const db = await dbPromise
  return db.transaction(localDb.imgStore).objectStore(localDb.imgStore).get(key)
}
export async function getAll () {
  const db = await dbPromise
  return db.transaction(localDb.imgStore).objectStore(localDb.imgStore).getAll()
}
export async function getOne () {
  const db = await dbPromise
  return db.transaction(localDb.imgStore).objectStore(localDb.imgStore).getAll(null, 1)
}
export async function setEntry (key, val) {
  const db = await dbPromise
  const tx = db.transaction(localDb.imgStore, 'readwrite')
  tx.objectStore(localDb.imgStore).put(val, key)
  return tx.complete
}
export async function deleteEntry (key) {
  const db = await dbPromise
  const tx = db.transaction(localDb.imgStore, 'readwrite')
  tx.objectStore(localDb.imgStore).delete(key)
  return tx.complete
}
export async function clear () {
  const db = await dbPromise
  const tx = db.transaction(localDb.imgStore, 'readwrite')
  tx.objectStore(localDb.imgStore).clear()
  return tx.complete
}
export async function keys () {
  const db = await dbPromise
  return db.transaction(localDb.imgStore).objectStore(localDb.imgStore).getAllKeys()
}
